<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="step = 0">Cancel</li>
    </ul>
    <ul class="header-button-right" v-if="step == 1">
      <li @click="step++">Next</li>
    </ul>
    <ul class="header-button-right" v-if="step == 2">
      <li @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container :step="step" />

  <div class="footer">
    <ul class="footer-button-plus">
      <input type="file" id="file" class="inputfile" @change="uploadFile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>
</template>

<script>
import Container from './components/Container.vue';
export default {
  name : "App",
  data(){
    return{
      step : 0
    }
  },
  methods : {
    uploadFile(e){
      let 파일 = e.target.files;
      let 사진 = URL.createObjectURL(파일[0]);

      this.$store.commit('addImg', 사진)
      this.step++
      console.log(사진)
    },
    publish(){
      this.step = 0;

      this.$store.commit('addPost')
    }
  },
  components : {
    Container
  }
}
</script>

