<template>
  <div 
  class="filter-item" 
  :class="item" 
  :style="{backgroundImage : `url(${picture})`}"
  @click="$store.commit('setFilter', item)"
  >
    {{item}}
</div> 
</template>

<script>
export default {
    name : "Filter",
    props : {
        item : String,
        picture : String
    }
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
  border: 1px solid #ccc;
  cursor: pointer;
  text-align: center;
  line-height: 100px;
}
</style>