import { createStore } from 'vuex'
import data from './data'

const store = createStore ({
  state(){
    return{
        count : 0, //사용 : {{$store.state.count}}
        post : [...data],
        newUser : {
            "name":"Kim Hyun",
            "userImage":"https://placeimg.com/100/100/arch",
            "postImage":"https://placeimg.com/640/480/arch",
            "likes":36,
            "date":"May 15",
            "liked":false,
            "content":"",
            "filter":"perpetua"
          }
    }
  },
  mutations : { //state 변경 함수 넣는곳 //  사용 : $store.commit('inclease')
    addImg(state,payload){
        state.newUser.postImage = payload

        // console.log(state.newUser)
        // state.post.unshift(user);
    },
    addText(state,payload){
        state.newUser.content = payload
        console.log(state.newUser)

    },
    addPost(state){
        state.post.unshift({...state.newUser});
    },
    setFilter(state, payload){
        state.newUser.filter = payload
    }
  }
//   actions : { //비동기 함수 넣는곳  // 사용 : $store.dispatch('getData')
//         getData(context){
//             axios.get('http://localhost:3000/post')
//             .then((a)=>{
//                 context.commit('setPost', a.data)
//             })
//         }
//   }
})

export default store