<template>
    <div v-if="step === 0">
        <Post v-for="(a, i) in post" :key="i" :item="a" />
        <div class="btn-center">
            <button v-if="moreClick < 2" @click="more">더보기</button>
        </div>
    </div>

    <div v-else-if="step === 1">
    <FilterPage />
    </div>
    <div v-else-if="step === 2">
    <WritePage />
    </div>
</template>

<script>
import { mapState } from "vuex";
import Post from "./Post.vue";
import axios from "axios";
import FilterPage from "./FilterPage.vue";
import WritePage from "./WritePage.vue";

export default {
    name: "Container",
    data() {
        return {
            moreClick: 0,
        };
    },
    props : {
        step : Number
    },
    computed: {
        ...mapState(["post"]),
    },
    methods: {
        more() {
            axios
                .get(
                    "https://codingapple1.github.io/vue/more" +
                        this.moreClick +
                        ".json"
                )
                .then((a) => {
                    this.moreClick++;
                    this.$store.commit("setPost", [a.data]);
                });
        },
    },
    components: {
        Post,
        FilterPage,
        WritePage,
    },
};
</script>

<style>
.btn-center {
    text-align: center;
}

.upload-image {
    width: 100%;
    height: 450px;
    background: cornflowerblue;
    background-size: cover;
}
.filters {
    overflow-x: scroll;
    white-space: nowrap;
}
.filter-1 {
    width: 100px;
    height: 100px;
    background-color: cornflowerblue;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color: white;
    background-size: cover;
}
.filters::-webkit-scrollbar {
    height: 5px;
}
.filters::-webkit-scrollbar-track {
    background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
    background: #555;
}
.write-box {
    border: none;
    width: 90%;
    height: 100px;
    padding: 15px;
    margin: auto;
    display: block;
    outline: none;
}
</style>